import React, { useCallback, useState } from 'react'
import { Button, Col, Row } from 'react-bootstrap'
import { SubtitleControl } from './SubtitleControl'
import { SubtitleStore } from '../store/sources'

export const SubtitleSetting = () => {
    const [isShow, setIsShow]  = useState(false)
    const [langKey, setLangKey]  = useState<keyof SubtitleStore>("en")

    const handleClose = useCallback(() => {
        setIsShow(false)
        setLangKey("en")
    }, [])

    if (!isShow) {
        return (
            <Button
                size='sm'
                onClick={() => setIsShow(true)}
            >
                 
            </Button>
        )
    }

    if (langKey === 'ru') {
        return (
            <>
                <Row>
                    <Col>
                        <h4>        </h4>
                        <SubtitleControl langKey={"ru"} />
                    </Col>
                </Row>
                <Row>
                    <Button onClick={handleClose} className="mt-1"></Button>
                </Row>
            </>
        )
    }

    return (
        <>
            <Row>
                <Col>
                    <h4>        </h4>
                    <SubtitleControl langKey={"en"} />
                </Col>
            </Row>
            <Row>
                <Button onClick={() => setLangKey('ru')} className="mt-1"></Button>
            </Row>
        </>
    )
}